export default {
  name: 'UpdateCampaign',
  data () {
    return {
      form: this.$form.createForm(this),
      formLayout: 'vertical'
    }
  },
  methods: {
    onSubmit () {
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log(values)
        }
      })
    },
    onChange (val) {
      console.log(val)
    }
  }
}



